.navbar-container {
  height: 7vh;
  display: grid;
  grid-template-columns: 20% 60% 20%;
}
nav {
  background-color: #fff;
  position: fixed;
  top: 0;
  width: 100%;
  text-align: center;
  border-bottom: 1px solid rgb(122, 147, 156);
}

nav ul {
  font-family: "Roboto Condensed", sans-serif;
  font-size: 1.1vw;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  color: rgb(122, 147, 156);
  align-self: center; /*** on vertical ***/
}
nav li {
  list-style: none;
  margin: 0 15px;
}
nav li:hover {
  background-color: #ffffff44;
}
nav a {
  color: inherit;
  text-decoration: none;
}
.active {
  visibility: visible;
  transition: all 0.5s;
}
.hidden {
  visibility: hidden;
  transition: all 0.5s;
  transform: translateY(-100%);
}
.cart-navbar {
  height: 3vh;
  margin-right: 20px;
  align-self: center; /*** on vertical ***/
  justify-self: end; /*** on gorizontal ***/
}
.App-logo1 {
  height: 5vh;
  margin-top: 5px;
  margin-left: 10vw;
  align-self: center; /*** on vertical ***/
  /* justify-self: end;  */ /*** on gorizontal ***/
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo1 {
    animation: App-logo-spin infinite 5s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}
